body {
  background-color: #fff;
  margin: 20px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.loader {
  justify-content: space-between;
  width: 108px;
  margin-left: 10px;
  display: flex;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  background-color: beige;
  background-image: radial-gradient(14px, #0d161b 100%, #0000 0);
  background-repeat: no-repeat;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  animation: 10s infinite eyeMove, 10s infinite blink;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 10% {
    background-position: 0 0;
  }

  13%, 40% {
    background-position: -15px 0;
  }

  43%, 70% {
    background-position: 15px 0;
  }

  73%, 90% {
    background-position: 0 15px;
  }

  93%, 100% {
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 48px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 18px;
  }
}

.error {
  color: red;
  font-style: italic;
}

.cat-info {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.cat-info img {
  cursor: pointer;
  border: 5px solid #87745b;
  border-radius: 20px;
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
  padding: 5px;
  transition: transform .5s;
}

.cat-info img:hover {
  transform: scale(1.1);
}

.cat-info h2 {
  color: #87745b;
  font-family: Georgia, Times New Roman, Times, serif;
}

.cat-info p {
  color: #87745b;
  font-size: 16px;
}

.cat-info #cat-temperament {
  font-style: italic;
}

.cat-info {
  margin: 20px;
}

#breed-select {
  color: #87745b;
  border: 3px dotted tan;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  font-size: 1em;
  font-weight: bold;
}

#breed-select:hover {
  color: #433a2f;
  border: 3px solid tan;
}

#breed-select option {
  color: #433a2f;
  font-weight: bold;
}

@media screen and (width >= 768px) {
  .cat-info img {
    width: 100%;
    max-width: 450px;
  }

  .cat-info h2 {
    font-size: 24px;
  }

  .cat-info p, #breed-select {
    font-size: 20px;
  }
}
